import { Component, EventEmitter, Input, Output } from '@angular/core';
import { MeetingDays, TimeSlots, selectedDays } from '@core/models/meeting-schedule.model';

@Component({
    selector: 'findqo-viewing-availability',
    templateUrl: './viewing-availability.component.html',
    styleUrl: './viewing-availability.component.scss',
    standalone: false
})
export class ViewingAvailabilityComponent {
	@Input() days: MeetingDays[];
	@Input() timeSlots: TimeSlots[];
	@Input() selectedDays: selectedDays[];

	@Output() handleChange = new EventEmitter();

	handleSlotChange(event): void {
		this.handleChange.emit(event);
	}
}
