import { Component, Inject, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Ad } from '@core/models';
import { DestroyService } from '@shared/services/destroy.service';
import { takeUntil } from 'rxjs';

@Component({
    selector: 'findqo-select-properties-dialog',
    templateUrl: './select-properties-dialog.component.html',
    styleUrl: './select-properties-dialog.component.scss',
    providers: [DestroyService],
    standalone: false
})
export class SelectPropertiesDialogComponent implements OnInit {
	constructor(
		@Inject(MAT_DIALOG_DATA) public data,
		private readonly dialogRef: MatDialogRef<SelectPropertiesDialogComponent>,
		private readonly destroy: DestroyService
	) {}

	searchControl = new FormControl('');
	filteredAds;
	noResult = false;

	handleSelection(ad: Ad) {
		this.dialogRef.close({ value: ad });
	}

	ngOnInit(): void {
		this.filteredAds = this.data?.data;

		this.searchControl.valueChanges.pipe(takeUntil(this.destroy)).subscribe((text) => {
			this.filteredAds = this.data.data.map((data) => ({
				...data,
				ads: data.ads.filter((ad) => ad.title.toLowerCase().includes(text.toLowerCase())),
			}));

			this.noResult = this.filteredAds.every((ad) => !ad.ads.length);
		});
	}
}
