import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
    selector: 'findqo-meeting-purpose',
    templateUrl: './meeting-purpose.component.html',
    styleUrl: './meeting-purpose.component.scss',
    standalone: false
})
export class MeetingPurposeComponent {
	@Input() selectedTypeId: string;

	meetingTypes = [
		{
			id: '1',
			name: 'Property on FindQo',
		},
		{
			id: '2',
			name: 'Property from other site',
		},
	];

	@Output() meetingTypeChange = new EventEmitter();

	handleChange(key: string, value: string): void {
		this.meetingTypeChange.emit({ key, value });
	}
}
