import { Component, EventEmitter, Inject, Input, Output, PLATFORM_ID } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MeetingAdsData } from '@core/models/meeting-schedule.model';
import { SelectPropertiesDialogComponent } from '../select-properties-dialog/select-properties-dialog.component';
import { MatDialog } from '@angular/material/dialog';

@Component({
    selector: 'findqo-select-property',
    templateUrl: './select-property.component.html',
    styleUrl: './select-property.component.scss',
    standalone: false
})
export class SelectPropertyComponent {
	constructor(
		private readonly dialog: MatDialog,
		@Inject(PLATFORM_ID) public plateformId
	) {}

	@Input() set selectedAd(ad) {
		this.selectedAdId = ad?.id;
		this.selectedAdTitle = ad?.title;
	}

	@Input() set adsData(adsData: MeetingAdsData) {
		this.filteredAds = adsData.data;
		this._adsData = adsData;

		if (this._adsData.isEmpty) {
			this.searchControl.disable();
		}
	}

	@Output() adSelectionChange = new EventEmitter();

	searchControl = new FormControl('');
	selectedAdTitle: string;
	selectedAdId: string;
	_adsData: MeetingAdsData;
	filteredAds;
	noResult = false;

	openBottomSheet(): void {
		this.dialog
			.open(SelectPropertiesDialogComponent, {
				data: this._adsData,
				width: '100%',
				height: '100%',
				maxWidth: '100vw',
			})
			.afterClosed()
			.subscribe((res) => {
				if (res) {
					this.handleChange({ value: res.value.id });
				}
			});
	}

	handleSearch($event) {
		if (!$event.target.value) {
			this.filteredAds = this._adsData.data;
			return;
		}

		this.filteredAds = this._adsData.data.map((data) => ({
			...data,
			ads: data.ads.filter(
				(ad) =>
					ad.title.toLowerCase().includes($event?.target.value?.toLowerCase()) ||
					ad.id?.includes($event?.target.value) ||
					ad.location?.address.toLowerCase().includes($event?.target.value.toLowerCase())
			),
		}));

		this.noResult = this.filteredAds.every((ad) => !ad.ads.length);
	}

	handleChange(event) {
		const AD = this._adsData.allAds.find((ad) => ad.id === event.value);
		if (AD) {
			this.selectedAdTitle = AD?.title;
			this.selectedAdId = AD?.id;
			this.adSelectionChange.emit({ key: 'adId', value: AD?.id });
			this.adSelectionChange.emit({ key: 'address', value: AD?.location?.address });
		}
	}
}
