import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { TimeSlotFieldKeys } from '@core/enums';
import { MeetingDays, TimeSlots, selectedDays } from '@core/models/meeting-schedule.model';
import { TimeSlotDialogComponent } from './components/time-slot-dialog/time-slot-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import { ViewportService } from '@core/services/viewport.service';

@Component({
    selector: 'findqo-time-slot',
    templateUrl: './time-slot.component.html',
    styleUrl: './time-slot.component.scss',
    standalone: false
})
export class TimeSlotComponent {
	constructor(
		private readonly fb: FormBuilder,
		private readonly dialog: MatDialog,
		public viewport: ViewportService
	) {}
	@Input() day: MeetingDays;
	@Input() timeSlots: TimeSlots[];
	@Input() set selectedDays(data: selectedDays[]) {
		this.alreadySelected = data.find((dt) => dt.dayId === this.day.id);

		if (this.alreadySelected) {
			this.form.patchValue({
				...this.alreadySelected.time[0],
			});
		}
	}
	@Output() onSelection = new EventEmitter();

	readonly formKeys = TimeSlotFieldKeys;
	alreadySelected: selectedDays;

	tooltipDescription =
		' With Auto Stretch Timeline turned on, we’ll automatically add more time slots if all current ones are booked up. For example, if all slots are between 17:30 and 18:00 with 5-minute intervals for each viewing, the system will add one interval slot before 17:30 and one interval slot after 18:00.';

	form: any = this.fb.group({
		startTimeId: [''],
		endTimeId: [''],
		autoStretchStartTime: [false],
		autoStretchEndTime: [false],
	});

	handleSelect(event, target) {
		let time = this.form.value;

		if (target === this.formKeys.DAY && !event.checked) {
			this.form.get('startTimeId').reset();
			this.form.get('endTimeId').reset();
			this.form.get('autoStretchStartTime').reset();
			this.form.get('autoStretchEndTime').reset();
		}

		if (target === this.formKeys.DAY) {
			if (!event.checked) {
				this.form.get('startTimeId').reset();
				this.form.get('endTimeId').reset();
			}

			time = {
				startTimeId: this.timeSlots[0].id,
				endTimeId: this.timeSlots[this.timeSlots.length - 1].id,
				autoStretchStartTime: this.form.get('autoStretchStartTime').value ? '1' : '0',
				autoStretchEndTime: this.form.get('autoStretchEndTime').value ? '1' : '0',
			};
		} else {
			time = {
				...time,
				[target]: event.value ?? (event.checked ? '1' : '0'),
			};
		}

		this.form.patchValue(time);

		this.onSelection.emit(
			JSON.parse(
				JSON.stringify({
					key: target,
					value: event.value ?? (event.checked ? '1' : '0'),
					dayId: this.day.id,
					timeSlots: {
						...time,
					},
				})
			)
		);
	}

	openBottomsheet(target) {
		let title;
		let timeSlotsList = [...this.timeSlots];

		switch (target) {
			case this.formKeys.START_TIME:
				title = 'n3784';
				timeSlotsList = timeSlotsList.slice(0, -1);
				break;

			case this.formKeys.END_TIME:
				title = 'n3783';
				timeSlotsList = timeSlotsList.slice(1);
				break;

			default:
		}

		this.dialog
			.open(TimeSlotDialogComponent, {
				data: { options: timeSlotsList, title, form: this.form.getRawValue(), target },
				width: '100%',
				height: '100%',
				maxWidth: '100vw',
			})
			.afterClosed()
			.subscribe((res) => {
				if (res) {
					this.handleSelect({ value: res.id }, target);
				}
			});
	}
}
